import {mapGetters, mapActions} from 'vuex';
import auth from '@/api/auth.js';

window.auth = auth;
export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters([
      'isLogin',
      'user'
    ])
  },
  created() {
    this.checkLogin();
  },
  methods: {
    ...mapActions([
      'checkLogin',
      'logout'
    ]),
    onLogout() {
      window.sessionStorage.clear();
      this.logout();
      this.router.push('/index')
    }
  }
};

